.galleryConts1 {
	position: relative;
	width: 100%;
	padding: 58px 0;
	background: #eff4ef;
	.outLine {
		height: 7px;
		border-top: solid 1px #6b6b6b;
		border-bottom: solid 1px #6b6b6b;
	}
	.inner {
		display: flex;
		flex-direction: row;
		width: 1140px;
		margin: 39px auto 0;
	}
	.item1 {
		width: 63%;
		margin-bottom: 42px;
		padding: 32px 63px 0px 0px;
		border-right: solid 1px #6b6b6b;
		.gallerySwiper {
			position: relative;
			.slide {
				width: 650px;
				height: 640px;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
		h2 {
			margin: 45px 0 26px;
			font-size: 13px;
			font-weight: 500;
			letter-spacing: 1.5px;
			color: #2b2b2b;
		}
		p {
			padding-bottom: 27px;
			line-height: 1.8;
			color: #3e3e3d;
			font-weight: 600;
			letter-spacing: 1px;
			font-family: "Cormorant", serif;
		}
		.viewBtn {
			display: flex;
			justify-content: flex-end;
			align-items: center;
			font-size: 13.5px;
			letter-spacing: 2.2px;
			font-weight: 500;
			cursor: pointer;
			span {
				padding-left: 10px;
			}
		}
	}
	.itemWrap {
		width: 37%;
	}

	.item2 {
		padding: 24px 60px 58px;
		border-bottom: 1px solid #6b6b6b;
		h1 {
			font-size: 37px;
			font-family: "Cormorant", serif;
			padding-bottom: 25px;
		}
		p {
			padding-bottom: 20px;
			line-height: 1.7;
			font-size: 18px;
			font-weight: 500;
			font-family: "Cormorant", serif;
		}
		.viewBtn {
			font-size: 14px;
			letter-spacing: 2px;
			span {
				padding-left: 10px;
			}
		}
	}

	.item3 {
		display: flex;
		padding: 67px 60px;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		.imgWrap {
			width: 295px;
			height: 295px;
			margin-bottom: 27px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		.imgTxt {
			width: 100%;
			display: flex;
			justify-content: space-between;
			font-size: 14px;
			letter-spacing: 2px;
			align-items: center;
			cursor: pointer;
			div {
				letter-spacing: 1px;
				font-weight: 500;
			}
			em {
				padding-left: 8px;
				font-family: "Cormorant", serif;
				font-size: 18px;
				font-weight: 900;
			}
		}
	}
}
.galleryConts2 {
	width: 100%;
	padding: 80px 0px 30px 30px;
	background: #f8f8f8;
	transition: all 1s;

	> * {
		transition: all 1.4s;
	}

	.inner {
		display: flex;
	}
	.left {
		width: 70%;

		.tit {
			display: flex;
			margin-bottom: 30px;
			align-items: center;

			h1 {
				font-size: 7.2vw;
				font-weight: 400;
				font-family: "Cormorant", serif;
			}
			> div {
				display: flex;
				width: 35%;
				margin-left: auto;
				margin-right: 30px;
				font-size: 17px;
				flex-direction: column;
				align-items: flex-end;
				text-align: right;
				.inputWrap {
					display: flex;
					padding-top: 20px;
				}
				input[type="text"] {
					height: 32px;
					padding-left: 12px;
					border: solid 1px #d4d4d4;
					border-radius: 8px 0 0 8px;
					&::placeholder {
						color: #b6b6b6;
					}
				}
				input[type="button"] {
					height: 32px;
					padding: 0 8px;
					color: black;
					background-color: #e5e5e5;
					border: none;
					border-radius: 0 8px 8px 0;
					font-size: 13px;
				}
			}
		}
		.masonry {
			height: 800px !important;
			scrollbar-width: thin;
			overflow-y: scroll;
			&::-webkit-scrollbar {
				width: 3px;
				background-color: rgba(211, 211, 211, 0.7);
			}
			&::-webkit-scrollbar-thumb {
				background: rgba(110, 110, 110, 0.7);
			}
			> li {
				width: 45%;
				margin-right: 5%;
				margin-bottom: 40px;
			}
			.imgWrap {
				margin-bottom: 20px;
				img {
					object-fit: cover;
					width: 100%;
					height: 100%;
				}
			}
			.tit {
				font-size: 18px;
				font-weight: 400;
				justify-content: space-between;
				img {
					width: 35px;
					margin-right: 15px;
					border-radius: 50px;
					border: solid 1px #ddd;
					box-shadow: 3px 3px 3px rgb(149, 149, 149);
				}
			}
			.profile {
				font-family: "Cormorant", serif;
			}
		}

		&.on {
			width: 70%;
		}
		&.off {
			width: 30%;

			.tit {
				margin-bottom: 75px;
				h1 {
					font-size: 4.6vw;
				}
				> div {
					display: none;
					.inputWrap {
						display: none;
					}
					input[type="text"] {
					}
					input[type="button"] {
					}
				}
			}

			.masonry {
				height: 800px !important;
				scrollbar-width: thin;
				overflow-y: scroll;
				&::-webkit-scrollbar {
				}
				&::-webkit-scrollbar-thumb {
				}
				> li {
					width: 93%;
					margin-right: 5%;
					margin-bottom: 50px;
				}
				.imgWrap {
					margin-bottom: 20px;
					img {
					}
				}
				.tit {
					font-size: 16px;
					font-weight: 300;
					img {
						width: 30px;
					}
				}
				.profile {
				}
			}
		}
	}
