	.right {
		width: 30%;
		padding-left: 30px;

		.tit {
			display: flex;
			margin-right: 30px;
			margin-bottom: 30px;
			align-items: center;
			text-align: right;

			h1 {
				font-size: 7.2vw;
				font-weight: 400;
				font-family: "Cormorant", serif;
			}
			> div {
				display: flex;
				width: 35%;
				margin-left: auto;
				font-size: 17px;
				flex-direction: column;
				align-items: flex-end;
				.inputWrap {
					display: flex;
					padding-top: 20px;
				}
				input[type="text"] {
					padding: 5px 10px;
					border: solid 1px #eee;
					border-radius: 10px;
				}
				input[type="button"] {
					font-size: 13px;
				}
			}
		}
		.masonry {
			height: 800px !important;
			scrollbar-width: thin;
			overflow-y: scroll;
			&::-webkit-scrollbar {
				width: 3px;
				background-color: rgb(211, 211, 211);
			}
			&::-webkit-scrollbar-thumb {
				background: rgb(110, 110, 110);
			}
			> li {
				width: calc(50% - 30px);
				margin-right: 5%;
				margin-bottom: 4vw;
			}
			.imgWrap {
				margin-bottom: 20px;
				img {
					object-fit: cover;
					width: 100%;
					height: 100%;
				}
			}
			.tit {
				display: flex;
				margin-bottom: 50px;
				margin-right: 0;
				font-size: 18px;
				font-weight: 400;
				justify-content: space-between;
				img {
					width: 35px;
					margin-right: 15px;
					border-radius: 50px;
					border: solid 1px #ddd;
					box-shadow: 3px 3px 3px rgb(149, 149, 149);
				}
			}
			.profile {
				font-family: "Cormorant", serif;
			}
		}

		&.on {
			width: 70%;
			.tit {
				> div {
					.inputWrap {
						padding-top: 20px;
					}
					input[type="text"] {
						height: 32px;
						padding-left: 12px;
						border: solid 1px #d4d4d4;
						border-radius: 8px 0 0 8px;
						&::placeholder {
							color: #b6b6b6;
						}
					}
					input[type="button"] {
						height: 32px;
